 } */
.main-container {
  display: grid;
  width: 100%;
  height: 100%;
  grid-template-areas: "logo nav nav aside aside" "navbar header header aside aside" "navbar main main statistic statistic";
  grid-template-rows: 4.375rem 1fr 1fr;
  grid-template-columns: 15rem 1fr 1fr 1.1fr;
  gap: 0.250rem; }

.statistic {
  grid-area: statistic;
  background-color: #fff; }

.main-nav__list {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center; }
  .main-nav__list__item {
    color: #000;
    font-size: 1.1rem;
    position: relative; }
    .main-nav__list__item small {
      font-size: 0.800rem; }
    .main-nav__list__item--sup {
      font-size: 0.720rem !important;
      position: absolute;
      top: -1rem;
      white-space: nowrap; }

.chart {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 1.563rem; }
  .chart__list {
    line-height: 2.2;
    color: #000;
    font-size: 0.9rem;
    margin-bottom: 1.875rem; }
    .chart__list__item::before {
      content: '';
      display: inline-block;
      width: 0.938rem;
      height: 0.938rem;
      border-radius: 50%;
      background-color: #1D0D6E;
      vertical-align: middle;
      margin-right: 0.313rem; }
    .chart__list__item:nth-child(2)::before {
      background-color: #AD2BB1; }
    .chart__list__item:nth-child(3)::before {
      background-color: #C8922C; }
    .chart__list__item:nth-child(4)::before {
      background-color: #E4F63C; }
    .chart__list__item:nth-child(5)::before {
      background-color: #74D06F; }
    .chart__list__item:nth-child(6)::before {
      background-color: #C82A6D; }
  .chart__circle {
    width: 11.875rem;
    height: 11.875rem;
    border-radius: 50%;
    margin: 0 auto;
    border: 0.625rem solid #E7E7E7; }

.statistic {
  padding: 1.875rem; }
  .statistic__wrapper {
    display: flex;
    justify-content: center;
    align-items: center; }
  .statistic__title {
    color: #5D5A5A;
    margin-bottom: 0.625rem; }
  .statistic__list {
    line-height: 2;
    color: #5D5A5A; }
    .statistic__list__item {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center; }
      .statistic__list__item__num {
        font-size: 2.813rem;
        color: #55E644;
        margin-right: 1.875rem; }
        .statistic__list__item__num:nth-child(2) {
          color: #EDD633; }
        .statistic__list__item__num:nth-of-type(3) {
          color: #DF2944; }
  .statistic__text {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    height: 13.5rem;
    line-height: 1.5; }

.header-row__text {
  padding: 0.9rem;
  padding-bottom: 1.700rem;
  font-size: 0.700rem;
  line-height: 1.188rem; }

.header-row__card {
  min-height: 7.5rem; }
